<template>
	<div id="ficView" class="ficView">	
	<div 
        class="fanfic"  
        
        :class="{
            fluttershyFanfic: $store.state.themes[0].isActive,
            twilightFanfic: $store.state.themes[1].isActive,
            celestiaFanfic: $store.state.themes[2].isActive,
            lunaFanfic: $store.state.themes[3].isActive,
        }"
       
    >
		
	    <div class="title">
            {{ selectedFanfic.name }}
        </div>

        <ul class="genres">
        	<li v-for="genre in selectedFanfic.genres">		
                <button @click="filterByGenre(genre)">
        			{{ genre }}
        		</button>
        	</li>
        </ul>
        
        <div class="wordCount">
            {{ selectedFanfic.wordCount }} слов
        </div>
        
        <div class="author">
            Автор:
            <a :href="selectedFanfic.authorLink" target="_blank">
                    {{ selectedFanfic.authorName }}
            </a>
        </div>
        <div class="nav">

            <ul id="fanfic-nav">
                <li v-for="chapter in selectedFanfic.chapters" :key="chapter.id">
                    <a :href="chapter.anchor">{{ chapter.name }}</a>
                </li>
            </ul>
        </div>
        <div class="sourceLink">      	
        		<a :href="selectedFanfic.sourceLink" target="_blank">
        			Открыть на ponyfiction.org
        		</a>
        </div>
        <div id="ficContent" v-if="!this.$store.state.isFicLoading">
            <ul id="fanfic">
                <li v-for="chapter in selectedFanfic.chapters" :key="chapter.id">
                    <span v-html="chapter.link"></span>
                    <span v-html="chapter.content"></span>
                </li>
            </ul>
        </div>
		<div class="preloader" v-else>
				<img src="@/arts/derpy_spinning.gif">
				<span>Loading...</span>
		</div>

	</div>
	<stickyBtn />
	</div>
	
</template>

<script>
	import selectList from '@/components/UI/selectList'
	import stickyBtn from '@/components/UI/stickyBtn.vue'

export default{
		name: 'ficList',

        components:{
           selectList,
           stickyBtn,
        },

        props:{
        		        
        },

        data() {
            return {

                ficHtml: 'Loading...',
                idByPath: this.$route.params.id,
                selectedFic: this.$store.state.libArray,
                sortedFics: this.$store.state.libArray,
                selectedFanfic: this.$store.state.libArray, 
            
            }
        },
        methods: {
        	filterByGenre(genre){
        		this.$store.state.filteredByGenre = this.sortedFics.filter((fic) => {
        			
        			return fic.genres.includes(genre);
                    
        		})
        		console.log(this.$store.state.filteredByGenre)
                this.$store.state.selectedGenre = genre;
                this.$store.state.noFilter = false;
                this.$store.state.genreFilter = true;
                this.$store.state.bookshelfVisible = true;
                this.$router.push({ path: `/` });
        		
        	},
        	selectedById(){
        		this.selectedFic = this.$store.state.libArray.filter(f => f.id == this.idByPath);
        		this.selectedFanfic = this.selectedFic[0];
        		console.log(this.selectedFic, this.selectedFanfic);


        	},
        	async fetchFic(){
            
                try{
                  this.$store.state.isFicLoading = true;
                  
                    for (let i = 0; i < this.selectedFanfic.chapters.length; i++){
                        const res = await fetch(this.selectedFanfic.chapters[i].path);

                        this.selectedFanfic.chapters[i].content = await res.text();                      
                    }
                  
        
                } catch (e) {
                    console.log(e)
                } finally {
                  this.$store.state.isFicLoading = false;
                }
        	}
        	
        },
        mounted(){
        	this.selectedById();
        	this.fetchFic();
        }

	}
</script>

